import { HeaderProps } from '@/types/layout';
import React from 'react';
import { Button } from '../ui/button';
import { Heading } from '../ui/typography';

function Header({ title, buttonGroups }: HeaderProps) {
  return (
    <header className="flex justify-between items-center h-[53px] pl-24 gap-8 pr-5">
      <div>
        {title ? (
          <Heading
            type="h1"
            className="text-foreground/80 dark:text-foreground/95"
          >
            {title}
          </Heading>
        ) : null}
      </div>
      <div className="gap-8 shrink-0 flex items-center toolbar">
        {buttonGroups?.map((buttons) => (
          <div className="flex gap-2 items-center">
            {buttons?.map((button) => {
              if (button.type === 'component') {
                if (React.isValidElement(button.component)) {
                  return button.component; // Fix: Return button.component instead of button
                }
              } else if (button.type === 'button') {
                return (
                  <Button
                    size={button.icon ? 'icon' : 'sm'}
                    variant={button.variant || 'ghost'}
                    key={`header-button-${button.title}`}
                    onClick={button.onClick}
                    title={button.title}
                  >
                    {button.icon ? button.icon : button.title}
                  </Button>
                );
              }
              return null;
            })}
          </div>
        ))}
      </div>
    </header>
  );
}

export default Header;
